/* BASICS */
.CodeMirror {
  font-family: inherit;
  color: #3a3c83;
  font-size: 1em;
  line-height: 1.43em;
}
.CodeMirror .CodeMirror-placeholder {
  color: #bcbde1;
}
.CodeMirror .CodeMirror-cursor {
  border-left: none;
  border-right: 2px solid #3a3c83;
  width: 0;
}
.CodeMirror .cm-header {
  color: #3a3c83;
}
.CodeMirror .cm-quote {
  color: #8586c9;
}
.CodeMirror .cm-negative {
  color: #f00;
}
.CodeMirror .cm-positive {
  color: #008080;
}
.CodeMirror .cm-header,
.CodeMirror .cm-strong {
  font-weight: bold;
}
.CodeMirror .cm-em {
  font-style: italic;
}
.CodeMirror .cm-link {
  text-decoration: underline;
}
.CodeMirror .cm-strikethrough {
  text-decoration: line-through;
}
.CodeMirror .cm-keyword {
  color: #ff0070;
}
.CodeMirror .cm-s-default .cm-atom {
  color: #219;
}
.CodeMirror .cm-s-default .cm-number {
  color: #164;
}
.CodeMirror .cm-s-default .cm-def {
  color: #00f;
}
.CodeMirror .cm-variable,
.CodeMirror .cm-punctuation,
.CodeMirror .cm-property,
.CodeMirror .cm-operator,
.CodeMirror .cm-variable-2,
.CodeMirror color $primary,
.CodeMirror .cm-s-default .cm-variable-3 {
  color: #085;
}
.CodeMirror .cm-comment {
  color: #8586c9;
}
.CodeMirror .cm-s-default .cm-string {
  color: #a11;
}
.CodeMirror .cm-s-default .cm-string-2 {
  color: #f50;
}
.CodeMirror .cm-s-default .cm-meta {
  color: #555;
}
.CodeMirror .cm-s-default .cm-qualifier {
  color: #555;
}
.CodeMirror .cm-s-default .cm-builtin {
  color: #30a;
}
.CodeMirror .cm-s-default .cm-bracket {
  color: #997;
}
.CodeMirror .cm-s-default .cm-tag {
  color: #bcbde1;
}
.CodeMirror .cm-s-default .cm-attribute {
  color: #00c;
}
.CodeMirror .cm-s-default .cm-hr {
  color: #999;
}
.CodeMirror .cm-link {
  color: #ff0070;
}
.CodeMirror .cm-error {
  color: #f00;
}
.CodeMirror .cm-invalidchar {
  color: #f00;
}
.CodeMirror .CodeMirror-composing {
  border-bottom: 2px solid;
/* PADDING */
}
.CodeMirror .CodeMirror-lines {
  padding: 4px 0 /* Vertical padding around content */;
}
.CodeMirror .CodeMirror pre {
  padding: 0 4px /* Horizontal padding of content */;
}
.CodeMirror .CodeMirror-scrollbar-filler,
.CodeMirror .CodeMirror-gutter-filler {
  background-color: #fff /* The little square between H and V scrollbars */;
}
.CodeMirror .CodeMirror-guttermarker {
  color: #000;
}
.CodeMirror .CodeMirror-guttermarker-subtle {
  color: #999;
}
