autocomplete {
  display: inline-block;
}
autocomplete input {
  width: 100%;
  box-sizing: border-box;
  border: none;
  color: #3a3c83;
}
autocomplete input::placeholder {
  color: #bcbde1;
  margin-left: 0.2em;
}
autocomplete .suggestions {
  list-style-type: none;
  cursor: default;
  padding: 0 2rem 0 0;
  margin: 0;
  border: none;
  background: #fff;
  position: absolute;
  z-index: 9999;
  max-height: 15rem;
  overflow: hidden;
  overflow-y: auto;
  box-sizing: border-box;
  color: #3a3c83;
}
autocomplete .suggestion {
  padding: 0 0.3rem;
  line-height: 1.5rem;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
autocomplete .suggestionhover,
autocomplete .suggestion.selected {
  color: #ff0070;
}
